'use client';

import { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Minus, Plus, Check } from 'lucide-react';
import { useAppDispatch, useAppSelector } from '@/lib/redux/hooks';
import { addToPosCart, updatePosCartQuantity, removeFromPosCart } from '@/lib/redux/features/posSlice';
import { cn } from '@/lib/utils';
import Image from 'next/image';

interface ProductCardProps {
  product: {
    id: string;
    name: string;
    price: number | string;
    images?: Array<{ url: string; alt?: string | null }>;
    stock: number;
    variants?: Array<{ id: string; price?: number | string | null }>;
  };
}

export function ProductCard({ product }: ProductCardProps) {
  const dispatch = useAppDispatch();
  const cart = useAppSelector((state) => state.pos.cart);
  const price = typeof product.price === 'string' ? parseFloat(product.price) : product.price;
  const imageUrl = product.images?.[0]?.url || '/placeholder-product.png';

  // Check if product is in cart (no variants for now)
  const cartItem = cart.find((item) => item.productId === product.id && !item.variantId);
  const quantity = cartItem?.quantity || 0;

  const handleAdd = () => {
    if (product.stock <= 0) return;

    if (quantity === 0) {
      dispatch(
        addToPosCart({
          id: `${product.id}-${Date.now()}`,
          productId: product.id,
          name: product.name,
          price,
          quantity: 1,
          image: imageUrl,
        })
      );
    } else {
      dispatch(
        updatePosCartQuantity({
          id: cartItem.id,
          quantity: quantity + 1,
        })
      );
    }
  };

  const handleDecrease = () => {
    if (cartItem && quantity > 1) {
      dispatch(
        updatePosCartQuantity({
          id: cartItem.id,
          quantity: quantity - 1,
        })
      );
    } else if (cartItem) {
      dispatch(removeFromPosCart(cartItem.id));
    }
  };

  const isOutOfStock = product.stock <= 0;

  return (
    <div
      className={cn(
        'border rounded-lg overflow-hidden bg-card hover:shadow-md transition-shadow',
        quantity > 0 && 'ring-2 ring-primary'
      )}
    >
      <div className="relative aspect-square bg-muted">
        {imageUrl && (
          <Image
            src={imageUrl}
            alt={product.name}
            fill
            className="object-cover"
            sizes="(max-width: 768px) 100vw, (max-width: 1200px) 50vw, 33vw"
          />
        )}
      </div>
      <div className="p-4">
        <h3 className="font-medium text-sm mb-1 line-clamp-2">{product.name}</h3>
        <p className="text-lg font-semibold mb-3">${price.toFixed(2)}</p>
        {quantity > 0 ? (
          <div className="space-y-2">
            <div className="flex items-center justify-between">
              <Button
                variant="outline"
                size="icon"
                className="h-8 w-8"
                onClick={handleDecrease}
                disabled={isOutOfStock}
              >
                <Minus className="h-4 w-4" />
              </Button>
              <span className="font-medium">{quantity}</span>
              <Button
                variant="outline"
                size="icon"
                className="h-8 w-8"
                onClick={handleAdd}
                disabled={isOutOfStock || product.stock <= quantity}
              >
                <Plus className="h-4 w-4" />
              </Button>
            </div>
            <Button
              variant="default"
              size="sm"
              className="w-full"
              disabled={isOutOfStock}
            >
              <Check className="h-4 w-4 mr-2" />
              Choose
            </Button>
          </div>
        ) : (
          <Button
            variant="default"
            size="sm"
            className="w-full"
            onClick={handleAdd}
            disabled={isOutOfStock}
          >
            <Plus className="h-4 w-4 mr-2" />
            Add Item
          </Button>
        )}
      </div>
    </div>
  );
}

