'use client';

import { useState } from 'react';
import { useAppSelector, useAppDispatch } from '@/lib/redux/hooks';
import { clearPosCart } from '@/lib/redux/features/posSlice';
import { Button } from '@/components/ui/button';
import { ScrollArea } from '@/components/ui/scroll-area';
import { Trash2, Maximize2 } from 'lucide-react';
import { removeFromPosCart, updatePosCartQuantity } from '@/lib/redux/features/posSlice';
import { PaymentModal } from './PaymentModal';
import Image from 'next/image';

const TAX_RATE = 0.1; // 10% tax
const DISCOUNT = 10; // $10 discount for demo

export function OrderSummary() {
  const dispatch = useAppDispatch();
  const cart = useAppSelector((state) => state.pos.cart);
  const orderType = useAppSelector((state) => state.pos.orderType);
  const [showPaymentModal, setShowPaymentModal] = useState(false);

  const subtotal = cart.reduce((sum, item) => sum + item.price * item.quantity, 0);
  const tax = subtotal * TAX_RATE;
  const discount = DISCOUNT;
  const total = subtotal + tax - discount;

  const handleRemove = (itemId: string) => {
    dispatch(removeFromPosCart(itemId));
  };

  const handleQuantityChange = (itemId: string, quantity: number) => {
    if (quantity <= 0) {
      dispatch(removeFromPosCart(itemId));
    } else {
      dispatch(updatePosCartQuantity({ id: itemId, quantity }));
    }
  };

  const handlePlaceOrder = () => {
    if (cart.length === 0) return;
    setShowPaymentModal(true);
  };

  return (
    <>
      <aside className="w-80 border-l bg-background flex flex-col">
        <div className="p-4 border-b flex items-center justify-between">
          <div>
            <h3 className="font-semibold">Order's Summary</h3>
            <p className="text-sm text-muted-foreground">#{Date.now().toString().slice(-6)}</p>
          </div>
          <Button variant="ghost" size="icon">
            <Maximize2 className="h-4 w-4" />
          </Button>
        </div>

        <div className="p-4 border-b">
          <p className="text-sm font-medium">Total Items ({cart.length})</p>
        </div>

        <ScrollArea className="flex-1 p-4">
          {cart.length === 0 ? (
            <div className="text-center text-muted-foreground py-8">
              Cart is empty
            </div>
          ) : (
            <div className="space-y-3">
              {cart.map((item) => (
                <div key={item.id} className="flex items-center gap-3 p-2 rounded-lg hover:bg-muted">
                  <div className="relative w-12 h-12 rounded overflow-hidden bg-muted flex-shrink-0">
                    {item.image && (
                      <Image
                        src={item.image}
                        alt={item.name}
                        fill
                        className="object-cover"
                        sizes="48px"
                      />
                    )}
                  </div>
                  <div className="flex-1 min-w-0">
                    <p className="text-sm font-medium truncate">{item.name}</p>
                    <p className="text-xs text-muted-foreground">
                      ${item.price.toFixed(2)} x {item.quantity}
                    </p>
                  </div>
                  <div className="flex items-center gap-2">
                    <Button
                      variant="ghost"
                      size="icon"
                      className="h-6 w-6"
                      onClick={() => handleQuantityChange(item.id, item.quantity - 1)}
                    >
                      <span className="text-xs">-</span>
                    </Button>
                    <span className="text-sm font-medium w-8 text-center">{item.quantity}</span>
                    <Button
                      variant="ghost"
                      size="icon"
                      className="h-6 w-6"
                      onClick={() => handleQuantityChange(item.id, item.quantity + 1)}
                    >
                      <span className="text-xs">+</span>
                    </Button>
                    <Button
                      variant="ghost"
                      size="icon"
                      className="h-6 w-6 text-destructive"
                      onClick={() => handleRemove(item.id)}
                    >
                      <Trash2 className="h-4 w-4" />
                    </Button>
                  </div>
                </div>
              ))}
            </div>
          )}
        </ScrollArea>

        <div className="p-4 border-t space-y-2">
          <div className="space-y-1 text-sm">
            <div className="flex justify-between">
              <span className="text-muted-foreground">Price:</span>
              <span>${subtotal.toFixed(2)}</span>
            </div>
            <div className="flex justify-between">
              <span className="text-muted-foreground">Taxes:</span>
              <span>${tax.toFixed(2)}</span>
            </div>
            <div className="flex justify-between">
              <span className="text-muted-foreground">Discount:</span>
              <span className="text-green-600">-${discount.toFixed(2)}</span>
            </div>
            <div className="flex justify-between font-semibold text-lg pt-2 border-t">
              <span>Total:</span>
              <span className="text-primary">${total.toFixed(2)}</span>
            </div>
          </div>
          <Button
            className="w-full"
            size="lg"
            onClick={handlePlaceOrder}
            disabled={cart.length === 0}
          >
            Place Order
          </Button>
        </div>
      </aside>

      {showPaymentModal && (
        <PaymentModal
          open={showPaymentModal}
          onClose={() => setShowPaymentModal(false)}
          total={total}
          subtotal={subtotal}
          tax={tax}
          discount={discount}
        />
      )}
    </>
  );
}

