'use client';

import { useState } from 'react';
import { useSession } from 'next-auth/react';
import { useAppDispatch, useAppSelector } from '@/lib/redux/hooks';
import { setOrderType } from '@/lib/redux/features/posSlice';
import { Button } from '@/components/ui/button';
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from '@/components/ui/dropdown-menu';
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar';
import { Package2, User } from 'lucide-react';
import { cn } from '@/lib/utils';

type OrderType = 'DINE_IN' | 'TAKE_AWAY' | 'DELIVERY';

const orderTypes: { value: OrderType; label: string }[] = [
  { value: 'DINE_IN', label: 'Dine In' },
  { value: 'TAKE_AWAY', label: 'Take Away' },
  { value: 'DELIVERY', label: 'Deliver' },
];

export function POSHeader() {
  const { data: session } = useSession();
  const dispatch = useAppDispatch();
  const currentOrderType = useAppSelector((state) => state.pos.orderType);
  const locationId = useAppSelector((state) => state.pos.locationId);

  return (
    <header className="h-16 border-b bg-background flex items-center justify-between px-6">
      <div className="flex items-center gap-4">
        <div className="flex items-center gap-2">
          <Package2 className="h-6 w-6 text-primary" />
          <span className="font-semibold text-lg">Admin POS</span>
        </div>
        <div className="flex items-center gap-2 ml-8">
          {orderTypes.map((type) => (
            <Button
              key={type.value}
              variant={currentOrderType === type.value ? 'default' : 'ghost'}
              size="sm"
              onClick={() => dispatch(setOrderType(type.value))}
              className={cn(
                'rounded-none border-b-2 border-transparent',
                currentOrderType === type.value && 'border-primary'
              )}
            >
              {type.label}
            </Button>
          ))}
        </div>
      </div>
      <div className="flex items-center gap-4">
        {locationId && (
          <span className="text-sm text-muted-foreground">
            Location: {locationId}
          </span>
        )}
        <DropdownMenu>
          <DropdownMenuTrigger asChild>
            <Button variant="ghost" className="flex items-center gap-2">
              <Avatar className="h-8 w-8">
                <AvatarImage src={session?.user?.image || ''} />
                <AvatarFallback>
                  {session?.user?.name?.charAt(0) || <User className="h-4 w-4" />}
                </AvatarFallback>
              </Avatar>
              <div className="text-left">
                <div className="text-sm font-medium">{session?.user?.name || 'Admin'}</div>
                <div className="text-xs text-muted-foreground">Cashier 01</div>
              </div>
            </Button>
          </DropdownMenuTrigger>
          <DropdownMenuContent align="end" className="w-56">
            <DropdownMenuLabel>My Account</DropdownMenuLabel>
            <DropdownMenuSeparator />
            <DropdownMenuItem>Settings</DropdownMenuItem>
            <DropdownMenuItem>Logout</DropdownMenuItem>
          </DropdownMenuContent>
        </DropdownMenu>
      </div>
    </header>
  );
}

